@tailwind base;
@tailwind components;
@tailwind utilities;


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


@layer components {
  .neomorphic-button {
    @apply shadow-[8px_8px_15px_#a3b1c6,-8px_-8px_15px_#ffffff]
           hover:shadow-[6px_6px_10px_#a3b1c6,-6px_-6px_10px_#ffffff]
           active:shadow-[inset_4px_4px_8px_#a3b1c6,inset_-4px_-4px_8px_#ffffff]
           transition-all duration-300 ease-in-out;
  }

  .neomorphic-input {
    @apply shadow-[inset_4px_4px_8px_#a3b1c6,inset_-4px_-4px_8px_#ffffff];
  }
}